<template>
  <div class="main-div">
    <!-- {{this.monthData}}
    <b-editable-table bordered class="editable-table" v-model="items" :fields="fields" @input-change="handleInput">
      <template #cell(isActive)="data">
        <span v-if="data.value">No</span>
        <span v-else>Yes</span>
      </template>
    </b-editable-table> -->
    <br>
    <b-row class="mt-1">
      <b-col cols="8">
        <b-table
          class="table table-bordered table-sm"
          selectable
          :select-mode="selectMode"
          @row-selected="onRowSelected"
          striped
          hover
          :items="items"
          :fields="fields"
        >
        </b-table>
      </b-col>
      <b-col cols="4">
        <b-row class="mt-1">
          <b-col cols="4" offset="2" class="col-left">
            <label for="input1">Month:</label>
          </b-col>
          <b-col cols="5">
            <b-form-input
              class="form-control form-control-sm required-color"
              id="input1"
              v-model="monthData.month"
              required
            ></b-form-input>
          </b-col>
        </b-row>
         <b-row class="mt-1">
          <b-col cols="4" offset="2" class="col-left">
            <label for="input2">Month Name:</label>
          </b-col>
          <b-col cols="5">
            <b-form-input
              class="form-control form-control-sm required-color"
              id="input2"
              v-model="monthData.monthName"
              required
            ></b-form-input>
          </b-col>
        </b-row>
        <b-row class="mt-1">
          <b-col cols="4" offset="2" class="col-left">
            <label for="input3">Year:</label>
          </b-col>
          <b-col cols="5">
            <b-form-input
              class="form-control form-control-sm required-color"
              id="input4"
              v-model="monthData.year"
              required
            ></b-form-input>
          </b-col>
        </b-row>
        <b-row class="mt-1">
          <b-col cols="4" offset="2" class="col-left">
            <label for="input5">Days:</label>
          </b-col>
          <b-col cols="5">
            <b-form-input
              class="form-control form-control-sm required-color"
              id="input5"
              v-model="monthData.days"
              required
            ></b-form-input>
          </b-col>
        </b-row>
        <b-row class="mt-1">
          <b-col cols="4" offset="2" class="col-left">
            <label for="input6">Status:</label>
          </b-col>
          <b-col cols="5">
            <b-form-select
              id="inline-form-custom-select-pref"
              class="mb-2 mr-sm-2 mb-sm-0"
              v-model="monthData.status"
              :options="[{ text: 'current', value: 'current' }, { text: 'closed', value: 'closed' }, { text: 'open', value: 'open' }]"
              :value="null">
          </b-form-select>
            <!-- <b-form-input
              class="form-control form-control-sm required-color"
              id="input6"
              v-model="monthData.status"
               :options="[{ text: 'current', value: current }, { text: 'closed', value: closed }, { text: 'open', value: open }]"
              required
            ></b-form-input> -->
          </b-col>
        </b-row>
        <br>
        <b-row>
          <b-col offset="2" cols="7">
           <b-button variant="dark" class="mr-1" @click.prevent="save()"
        >Update Month Data</b-button>
        </b-col>
        </b-row>
      </b-col>
    </b-row>
    <!-- {{this.items}} -->
  </div>
</template>

<script>
// import BEditableTable from 'bootstrap-vue-editable-table'
import axios from 'axios'
export default {
  components: {
    // BEditableTable
  },
  data () {
    return {
      monthData: {},
      selected: {},
      selectMode: 'single',
      errors: [],
      fields: [
        // {
        //   key: 'id',
        //   label: 'Id',
        //   type: 'text',
        //   sortable: false,
        //   placeholder: '',
        //   class: 'name-col'
        // },
        {
          key: 'month',
          label: 'Month',
          type: 'text',
          sortable: true,
          placeholder: 'Enter Month...',
          class: 'name-col'
        },
        {
          key: 'monthName',
          label: 'Month Name',
          type: 'text',
          sortable: true,
          placeholder: 'Enter Name...',
          class: 'name-col'
        },
        {
          key: 'year',
          label: 'Year',
          type: 'text',
          sortable: true,
          placeholder: 'Enter Name...',
          class: 'name-col'
        },
        {
          key: 'days',
          label: 'No. of Days',
          type: 'text',
          sortable: true,
          placeholder: 'Enter Name...',
          class: 'name-col'
        },
        {
          key: 'status',
          label: 'Status',
          type: 'select',
          editable: true,
          placeholder: 'Enter Name...',
          class: 'name-col',
          options: [
            { value: 'closed', text: 'closed' },
            { value: 'current', text: 'current' }
          ]
        }
      ],
      items: []
    }
  },
  mounted () {
    this.fetchMonthData()
  },
  methods: {
    onRowSelected (items) {
      this.selected = items[0]
      this.monthData = this.selected
    },
    checkForm: function (e) {
      console.log('checking form data')
      this.errors = []
      console.log(this.monthData.month < 1 || this.monthData.month > 12)
      if (this.monthData.month < 1 || this.monthData.month > 12) {
        this.errors.push('Value of month should be between 1 and 12')
        console.log('érror')
      }
      // if (!this.leaveTransaction.leaveNature) {
      //   this.errors.push('Leave Nature required.')
      // }
      // if (!this.leaveTransaction.leaveType) {
      //   this.errors.push('Leave Type Required.')
      // }
      // if (!this.leaveTransaction.dateFrom) {
      //   this.errors.push('Leave date from Required.')
      // }
      // if (!this.leaveTransaction.dateTo) {
      //   this.errors.push('Leave date to Required.')
      // }
      console.log(!this.errors.length)
      if (!this.errors.length) {
        return true
      } else {
        this.errors.unshift(
          'Incorrent values, please check the following ..'
        )
        this.$toasted.show(this.errors, {
          theme: 'bubble',
          position: 'top-right',
          duration: 5000
        })
      }
    },
    houseKeeping () {
      this.errors = []
    },
    async handleInput (value) {
      console.log(value.item.status)
      this.monthData = {
        id: value.item.id,
        month: value.item.month,
        monthName: value.item.monthName,
        year: value.item.year,
        days: value.item.days,
        status: value.item.status
      }
      this.save()
    },
    async save () {
      // console.log('save')
      this.checkForm()
      if (!this.errors.length) {
        console.log('saving')
        await this.updateData()
      }
      this.houseKeeping()
    },
    async updateData () {
      alert('update month')
      const url = process.env.VUE_APP_API_URL + '/month'
      await axios
        .post(url, this.items, {
          headers: {
            'Content-Type': 'application/json'
          }
        })
        .then((response) => {
          this.msg = {}
          //   commit('setMessage', 'Sucess')
          this.isBusy = !this.isBusy
          this.$toasted.show('Month data updated sucessfully!', {
            theme: 'bubble',
            position: 'top-right',
            duration: 5000
          })
        })
        .catch((error) => {
          console.log(error.response.data)
          this.msg =
            'Error:' + JSON.stringify(error.response.data.errorMessage)
        })
    },
    async fetchMonthData () {
      // alert(id)
      const url = process.env.VUE_APP_API_URL + '/month'
      await axios
        .get(url)
        .then((response) => {
          return response
        })
        .then((response) => {
          this.items = response.data
          this.msg = {}
        })
        .catch((error) => {
          console.log(error.response.data)
          this.msg = error.response.data.errorMessage
        })
    }
  }
}
</script>

<style scoped>
.main-div {
  width: 85%;
  margin-left: 100px;
  margin-right: 100px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: large;
}
</style>
